import {CUSTOM_ELEMENTS_SCHEMA, NgModule} from '@angular/core';
import {BrowserModule} from '@angular/platform-browser';
import {RouteReuseStrategy} from '@angular/router';
import {IonicModule, IonicRouteStrategy} from '@ionic/angular';
import {SplashScreen} from '@ionic-native/splash-screen/ngx';
import {StatusBar} from '@ionic-native/status-bar/ngx';
// import { IonicApp, IonicModule } from 'ionic-angular';
import {AppRoutingModule} from './app-routing.module';
import {AppComponent} from './app.component';
import {HTTP_INTERCEPTORS, HttpClientModule} from '@angular/common/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
// import { MatSlideToggleModule } from '@angular/material/slide-toggle';
// import { MatMenuModule } from '@angular/material/menu';
// import { MatSidenavModule } from '@angular/material/sidenav';
// import { MatBadgeModule } from '@angular/material/badge';
import { MatButtonModule } from '@angular/material/button';
import {FormatTimePipe} from './shared-components/custom-pipes/count-down-pipe';
import {LogoSpinnerModule} from './shared-components/logo-spinner/logo-spinner.module';
import {HttpTokenInterceptorService} from './shared-components/authentication/http-token-interceptor.service';
import {LoginGuard, PreLogin} from './shared-components/authentication/auth-guards.service';
// import {FormatTimePipe} from './shared-components/custom-pipes/count-down-pipe';
@NgModule({
    declarations: [AppComponent],
    entryComponents: [],
    imports: [
        BrowserModule,
        HttpClientModule,
        IonicModule.forRoot(),
        AppRoutingModule,
        BrowserAnimationsModule,
        MatButtonModule,
        LogoSpinnerModule
    ],
    providers: [
        StatusBar,
        SplashScreen,
        LoginGuard,
        PreLogin,
        {provide: RouteReuseStrategy, useClass: IonicRouteStrategy},
        { provide: HTTP_INTERCEPTORS, useClass: HttpTokenInterceptorService , multi: true },
    ],
    bootstrap: [AppComponent],
    schemas: [CUSTOM_ELEMENTS_SCHEMA]
})
export class AppModule {
}
